import React, { Fragment, useState } from "react";

const EditTodo = ({ todo }) => {
  const [description, setDescription] = useState(todo.description);
  const [h, seth] = useState("none");

  //edit description function

  const updateDescription = async (id) => {
   
    let oh = {
      des:description,
      idous:id
    }
    console.log(JSON.stringify(`${oh}`))
    try {
      const body = description ;

      const response = await fetch(
        `https://ntodo.herokuapp.com/todos/${id}`,
        {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(oh)
        }
      );

      window.location = "/";
    } catch (err) {
      console.error(err.message);
    }
  };

  return (
    <Fragment>
      <button
        type="button"
        id="edittodo"
        class="btn "
        data-toggle="model"
        data-target={`${todo.todo_id}`}
        
        onClick={() =>{
            seth("block")

        }}
      >
        Edit
      </button>
              
      {/* 
        id = id10
      */}
      <div
        class="animate"
        id="add2"
        onClick={() => setDescription(todo.description)}
        style={{display:h}}
      >
        <div class="modal-dialog">
          
            
           
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={() => setDescription(todo.description)}
              >
                &times;
              </button>
            </div>

            <div class="modal-body">
              <input
                type="text"
                className="form-control"
                value={description}
                onChange={e => {
                    
                         setDescription(e.target.value)
                    
                   
                }}
              />
            </div>

            <div class="modal-footer">
              
                 <button
                  className="btn btn-danger"
                  onClick={() => updateDescription(todo.todo_id)}
                >
                  edit
                </button>
            
              <button
                type="button"
                class="btn btn-danger"
                data-dismiss="modal"
                onClick={() => {
                    setDescription(todo.description)
                    seth("none")
                
                }}
              >
                Close
              </button>
            </div>
          </div>
      
    
    </Fragment>
  );
};

export default EditTodo;